import { computed, makeObservable, observable } from "mobx";
import { MenuItemProps } from "semantic-ui-react";
import { CourseModel, CourseStatus } from "../../../../models/CourseModel";
import { PlannerViewModel } from "../../PlannerViewModel";

export class ContextMenuViewModel {
  constructor(plannerViewModel: PlannerViewModel) {

    makeObservable<ContextMenuViewModel, "_isContextOpen">(this, {
      _isContextOpen: observable,
      isContextOpen: computed,
    })

    this._plannerViewModel = plannerViewModel;
    this._contextMenuItems = this.getContextMenuItems();
  }

  private _plannerViewModel: PlannerViewModel;

  private _isContextOpen = false;
  get isContextOpen() {
    return this._isContextOpen;
  }
  set isContextOpen(state: boolean) {
    this._isContextOpen = state;
  }

  private _contextElement: any;
  get contextElement(): any {
    return this._contextElement;
  }
  set contextElement(element: any) {
    this._contextElement = element;
  }

  getContextMenuItems(course?: CourseModel): MenuItemProps[] {
    const areMultipleItems = this._plannerViewModel.selectedCourses.length > 1;
    const menuItems: { [key: string]: MenuItemProps } = {
      remove: {
        key: 'remove',
        content: `Remove ${areMultipleItems ? "all courses" : "course"}`,
        icon: 'delete calendar',
        onClick: () => {
          const moveSelectedToRemoved = () => {
            this._plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setUnplanned());
            this._plannerViewModel.unselectAllCourses();
          }
          if (!course) {
            moveSelectedToRemoved();
          } else {
            if (this._plannerViewModel.selectedCourses.includes(course)) moveSelectedToRemoved();
            course.setUnplanned();
          }
        }
      },
      plan: {
        key: 'plan',
        content: `Move ${areMultipleItems ? "all courses" : "course"} to planned`,
        icon: 'bookmark',
        onClick: () => {
          const moveSelectedToPlanned = () => {
            this._plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setPlanned());
          }
          if (!course) {
            moveSelectedToPlanned();
          } else {
            if (this._plannerViewModel.selectedCourses.includes(course)) moveSelectedToPlanned();
            course.setPlanned();
          }
        }
      },
      complete: {
        key: 'complete',
        content: `Move ${areMultipleItems ? "all courses" : "course"} to completed`,
        icon: 'checked calendar',
        onClick: () => {
          const moveSelectedToCompleted = () => {
            this._plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setCompleted());
          }
          if (!course) {
            moveSelectedToCompleted();
          } else {
            if (this._plannerViewModel.selectedCourses.includes(course)) moveSelectedToCompleted();
            course.setCompleted();
          }
        }
      },
    };
    if (course) {
      if (course.courseStatus === CourseStatus.UNPLANNED) {
        const { remove, ...items } = menuItems;
        return Object.values(items);
      } else if (course.courseStatus === CourseStatus.PLANNED) {
        const { plan, ...items } = menuItems;
        return Object.values(items);
      } else if (course.courseStatus === CourseStatus.COMPLETED) {
        const { complete, ...items } = menuItems;
        return Object.values(items);
      }
    }
    return Object.values(menuItems);
  }

  private _contextMenuItems: MenuItemProps[] = [];
  get contextMenuItems(): MenuItemProps[] {
    return this._contextMenuItems;
  }
  set contextMenuItems(items: MenuItemProps[]) {
    this._contextMenuItems = items;
  }

  openContext = (event: MouseEvent) => {
    this._contextElement = {
      getBoundingClientRect: () => ({
        left: event.clientX,
        top: event.clientY,
        right: event.clientX + 1,
        bottom: event.clientY + 1,
        height: 0,
        width: 0,
      }),
    }
    this.isContextOpen = true;
  }

  onWindowContextMenu = (event: MouseEvent) => {
    if (event.defaultPrevented) return;

    // Don't use context when no items selected. Possibly change this.
    if (this._plannerViewModel.selectedCourses.length === 0) return;

    event.preventDefault();
    this.contextMenuItems = this.getContextMenuItems();

    this.openContext(event);
  }

  onCourseContextMenu = (course: CourseModel, event: MouseEvent) => {
    // If course not in selectedCourses, toggle selection
    if (!this._plannerViewModel.selectedCourses.find((selectedCourse: CourseModel) => selectedCourse.id === course.id)) {
      this._plannerViewModel.toggleSelection(course);
    }

    event.preventDefault()
    this.contextMenuItems = this.getContextMenuItems(course);

    this.openContext(event);
  }
}