import { observer, Observer } from "mobx-react";
import { Popup, Menu } from "semantic-ui-react";
import { ContextMenuViewModel } from "./ContextMenuViewModel";

const ContextMenu = observer(({ contextMenuViewModel }: { [key: string]: ContextMenuViewModel }) => {
  return <Observer>
    {() => {
      return (
        <Popup
          basic
          context={contextMenuViewModel.contextElement}
          onClose={() => contextMenuViewModel.isContextOpen = false}
          open={contextMenuViewModel.isContextOpen}
        >
          <Menu
            items={contextMenuViewModel.contextMenuItems}
            onItemClick={(e) => {
              e.preventDefault();
              contextMenuViewModel.isContextOpen = false;
            }}
            secondary
            vertical
          />
        </Popup>
      )
    }}
  </Observer>
})

export default ContextMenu;